html {
  background: #000;
  color: #fff;
  font-size: calc(100vw / 1920 * 10);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  user-select: none;
  width: 100%;
}


canvas {
  height: 100%;
  left: 0;
  opacity: 1;
  position: fixed;
  top: 0;
  transition: opacity 1s ease;
  width: 100%;

}

.gallery {
  height: 100%;
  position: relative;
  width: 630rem;
  visibility: hidden;
}

.gallery__figure {
  position: absolute;

  &:nth-child(1) {
    height: 80rem;
    left: 0;
    top: 0;
    width: 53rem;
  }

  &:nth-child(2) {
    height: 70rem;
    left: 73rem;
    bottom: 0;
    width: 47rem;
  }

  &:nth-child(3) {
    height: 90rem;
    left: 140rem;
    top: 10rem;
    width: 60rem;
  }

  &:nth-child(4) {
    height: 75rem;
    left: 220rem;
    top: 0;
    width: 50rem;
  }

  &:nth-child(5) {
    bottom: 0;
    height: 75rem;
    left: 290rem;
    width: 50rem;
  }

  &:nth-child(6) {
    height: 75rem;
    left: 360rem;
    top: 20rem;
    width: 50rem;
  }

  &:nth-child(7) {
    height: 75rem;
    left: 430rem;
    top: 30rem;
    width: 70rem;
  }

  &:nth-child(8) {
    height: 427px;
    left: 520rem;
    top: 50%;
    width: 640px;
    transform: translate3d(0,-50%,0);
  }
}

.gallery__image {
  height: 100%;
  left: 0;
  object-fit: cover;
  position: absolute;
  top: 0;
  width: 100%;
}
